<template>
    <div id="header">
        <link rel="stylesheet" href="https://fonts.googleapis.com/icon?family=Material+Icons">
        <div>
            <h2 id="logo"><router-link to="/">DJARI CLOTHE</router-link></h2>
        </div>
        <div id="nav-bar">
            <ul>
                <li><a class="nav-underline" href="#">Nosotros</a></li>
                <li><a class="nav-underline" href="/categories">Catálogo</a></li>
                <li><a class="nav-underline" href="#">Afíliate</a></li>
            </ul>
        </div>
        <div id="icons">
            <a :href="user_session ? '/profile' : '/login' "><span id="user" class="material-icons md-24">person_outline</span></a>
            <a href="/favorites"><span class="material-icons md-24">favorite</span></a>
            <a href="/shopcar"><span class="material-icons md-24">shopping_cart</span></a>
    
        </div>
    </div>
</template>

<script>
    export default{
        name: 'NewHeader',
        data(){
            return {
                user_session: false,
            }
        },
        mounted(){
            this.checkSession();
        },
        methods: {
            checkSession(){
                const session = localStorage.getItem("user_session");
                if(session != null){
                    //console.log("Si hay sesion");
                    const user_icon = document.getElementById("user");
                    user_icon.textContent = "person";
                    this.user_session = true;
                }
            }
        }
    }
</script>

