    display: flex;
    background: black;
    color: white;
    justify-content: space-between;
    align-items: center;
    padding: 15px 70px 15px 150px;
}

h2{
    font-size: 20px;
    font-weight: bold;
}

#nav-bar > ul{
    list-style-type: none;
    display: flex;
    align-items: center;
    gap: 40px;
    margin-right: 5rem;
    padding-top: 10px;
    font-weight: 400;
}

.nav-underline:hover{
    text-decoration: underline;
}

